import Exp from "../Data/Exp";
import Edu from "../Data/Edu";

const Resume = () => {
    return (
        <div className="container-fluid">
            <div className="head">
                <h1>Resume</h1>
                <hr />
            </div>
            <div className="main">
                <div className="row">
                    <div className="col-12 col-md-7 col-lg-7">
                        <div className="main-left">
                            <h1 className="display-4">Experience</h1>
                            <hr />
                            {Exp && Exp.map((e) => (
                                <div key={e.key} className="exp">
                                    <h1 className="title">
                                        <a href={e.url}>
                                            {e.title}
                                        </a>
                                        <span className="time">{e.time}</span>
                                    </h1>
                                    <ul className="tasklist">
                                        {e.tasklist.map((t) => (
                                            <li key={t.key} className="task">{t.task}</li>
                                        ))}
                                    </ul>
                                    <hr />
                                </div>
                            ))}
                        </div>
                    </div>
                    <div className="col-12 col-md-5 col-lg-5">
                        <div className="main-right">
                            <h1 className="display-4">Education</h1>
                            <hr />
                            {Edu && Edu.map((e) => (
                                <div key={e.key} className="edu">
                                    <h1 className="title">{e.title}</h1>
                                    <span className="time">{e.time}</span>
                                    <h1 className="college">{e.college}<span className="major">{e.major}</span></h1>
                                    <hr />
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div >
    )
}

export default Resume;