import { Link } from "react-router-dom";
import Navs from "../Data/Navs";

const Navbar = ({ theme, setTheme }) => {
    const themeToggler = () => {
        theme === "light" ? setTheme("dark") : setTheme("light");
    };


    return (
        <nav className="navbar navbar-expand-lg navbar-light">
            <div className="container">
                <div className="mode">
                    {theme === "light" ? <i onClick={themeToggler} className="btn fas fa-toggle-off"></i> : <i onClick={themeToggler} className="btn fas fa-toggle-on"></i>}
                </div>
                {theme === "light" ? <Link className="navbar-brand text-dark" to="/">Kunal Srivastava</Link> : <Link className="navbar-brand text-light" to="/">Kunal Srivastava</Link>}
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <lord-icon
                        src="https://cdn.lordicon.com/phtfmmnb.json"
                        trigger="morph"
                        colors="primary:#8A2BE2,secondary:#8A2BE2">
                    </lord-icon>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                        {Navs && Navs.map((nav) => (

                            <Link to={nav.path} key={nav.id} className="nav-item btn mx-1">
                                <lord-icon
                                    src={nav.icon}
                                    trigger="loop"
                                    colors="primary:#8A2BE2,secondary:#8A2BE2">
                                </lord-icon>{nav.name}</Link>
                        ))}
                    </ul>
                </div>
            </div>
        </nav>
    )
}

export default Navbar;