import Content from "../Data/Content";
import Skill from "../Data/Skill";

const Home = () => {
    return (
        <div className="container-fluid">
            <div className="head">
                <h1>Home</h1>
                <hr />
            </div>
            <div className="main">
                <div className="row">
                    <div className="col-12 col-md-7 col-lg-8">
                        <div className="main-left">
                            <h1 className="display-4">Left</h1>
                            <hr />
                            {Content && Content.map((c) => (
                                <p key={c.key}>{c.p}</p>
                            ))}
                        </div>
                    </div>
                    <div className="col-12 col-md-5 col-lg-4">
                        <div className="main-right">
                            <h1 className="display-4">Skills</h1>
                            <hr />
                            {Skill && Skill.map((s) => (
                                <div key={s.key} className="skills">
                                    <h1 className="skill">
                                        {s.title}
                                    </h1>
                                    <div className="progress">
                                        <div className="progress-bar" role="progressbar" aria-valuenow="100"
                                            aria-valuemin="0" aria-valuemax="100" style={{ width: s.level }}>
                                            {s.level}
                                        </div>
                                    </div>
                                </div>
                            ))}

                        </div>
                    </div>
                </div>
            </div>
        </div >
    )
}

export default Home;